import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue';
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue';
import navBar from '@/components/zhouWei-navBar';
import publicFnc from '@/static/js/public.js';
export default {
	data() {
		return {
			animationData: {},
			animation: {},
			userData: [],
			options: [
				{
					text: '取消',
					style: {
						backgroundColor: '#007aff'
					}
				},
				{
					text: '确认',
					style: {
						backgroundColor: '#dd524d'
					}
				}
			],
			show:true,
		};
	},
	methods: {
		butAimation() {
			var animation = uni.createAnimation({
				duration: 150,
				timingFunction: 'ease',
				delay: 0
			});
			this.animation = animation;
			this.animation.opacity('0.1').step();
			this.animation.opacity('1').step();
			this.animationData = this.animation.export();
		},
		add() {
			this.butAimation();
			uni.navigateTo({
				url: '/pages/user/adduser/index',
				animationType: 'pop-in',
				animationDuration: 200
			});
		},
		getHuiData() {
			try {
				let keysArray = uni.getStorageSync('huizi_keys');
				let len = keysArray.length;
				let huizi_data = [];
				if (keysArray) {
					for (let i = 0; i < len; i++) {
						uni.getStorage({
							key: i + '_key',
							success: function(res) {
								//huizi_data[i] = res.data;小程序中无法使用！
								huizi_data.push(res.data);
							}
						});
					}
					this.userData = huizi_data;
				}
			} catch (e) {
				// error
			}
		},
		toDetail(id) {
			uni.navigateTo({
				//唯一ID值传入userdetail页面
				url: '/pages/user/userdetail/index?id=' + id,
				animationType: 'pop-in',
				animationDuration: 200
			});
		},
		onClick(id ) {
			publicFnc.customFnc.remove(id);
			uni.switchTab({
				url: "/pages/index/index",
				success: function(e) {
					var page = getCurrentPages().pop();
					if (page == undefined || page == null) return;
					if( page.hasOwnProperty('onLoad') ){
						page.onLoad();
					}else{
						location.reload();
						
					}
				}
			});
			// uni.navigateTo({
			// 	url: '/pages/user/blank/index',
			// });
			
		},
		change(open) {
			console.log('当前开启状态：' + open);
		}
	},
	onShow() {
		this.getHuiData();
	},
	components: {
		navBar
	}
};
